    .city span:hover {
      color: #f36421; }
  .city__selected {
    position: relative;
    z-index: 2;
    background-color: #ffffff;
    border-radius: 5px; }
    .city__selected::before, .city__selected::after {
      position: absolute;
      top: 50%;
      font-family: 'Flaticon';
      font-weight: 600;
      font-style: normal;
      z-index: 2;
      color: #f36421; }
    .city__selected::before {
      content: '\f103';
      left: 15px;
      font-size: 20px;
      transform: translateY(-50%); }
    .city__selected::after {
      content: '\f101';
      left: calc(100% - 15px);
      font-size: 15px;
      transform: translate(-100%, -50%);
      transition: transform .5s ease; }
  .city__wrapper {
    display: none;
    position: relative;
    z-index: 1;
    top: -25px;
    padding-top: 25px;
    background-color: #ffffff;
    border-radius: 5px; }
  .city__item {
    opacity: 0;
    transition: opacity .5s ease; }
    .city__item.show {
      opacity: 1; }
    .city__item.hidden {
      display: none !important;
      opacity: 0 !important; }

.mobile-menu {
